.btn-yellow {
  font-size: 14px;
  line-height: 105%;
  font-weight: 700;
  text-transform: uppercase;
  color: #081529;
  background-color: #ffba01;
  &:hover {
    background-color: #c9a028;
  }
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.display2 {
  font-size: 40px;
  line-height: 120%;
  font-weight: 700;
  text-transform: uppercase;
  color: #081529;
  padding-left: 16px;
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    width: 6px;
    height: calc(100% - 15px);
    background-color: #ffba01;
  }
}
.services-cards {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 30px;
  margin-top: 40px;
}
.services-card {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 186, 1, 0.7);;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  &:hover {
    .services-card__title {
      display: none;
    }
    .services-card__price {
      display: none;
    }
    .services-card__btn {
      display: block;
    }
    .services-hover {
      display: block;
    }
    &:after {
      opacity: 1;
      pointer-events: all;
    }
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__desc {
    position: absolute;
    z-index: 2;
    left: 20px;
    bottom: 20px;
    padding-right: 20px;
  }
  &__title {
    font-size: 19px;
    line-height: 130%;
    font-weight: 700;
    text-transform: uppercase;
    color: #ffffff;
  }
  &__btn {
    width: 140px;
    height: 46px;
    background-color: #081529;
    font-size: 14px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    color: #ffba01;
    display: none;
  }
  &__price {
    font-size: 14px;
    line-height: 105%;
    color: #081529;
    font-weight: 700;
    background: #ffba01;
    padding: 3px 5px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin-top: 5px;
  }
}
.services-hover {
  position: absolute;
  z-index: 2;
  top: 20px;
  left: 20px;
  display: none;
  &__title {
    font-size: 16px;
    line-height: 130%;
    font-weight: 700;
    text-transform: uppercase;
    color: #ffffff;
  }
  &__price {
    font-size: 14px;
    line-height: 105%;
    color: #081529;
    font-weight: 700;
    background: #ffffff;
    padding: 3px 5px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
  }
}
.services-btn {
  width: 130px;
  height: 46px;
  margin: 30px auto 0;
}
.nav {
  padding: 50px 0;
  background-color: #081529;
  height: 100%;
  &__list li {
    font-size: 20px;
    line-height: 120%;
    color: #ffffff;
    &:hover {
      color: #ffba01;
    }
    &:not(:first-child) {
      margin-top: 15px;
    }
  }
}
.modal {
  background: rgba(#000, .4);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: 580px;
    max-width: 100%;
    position: relative;
    background: #ffffff;
    margin: 0 auto;
    padding: 40px 30px;
    @media screen and (max-width: 992px) {
      margin: 50px auto 0;
      max-width: calc(100% - 30px);
    }
  }
  &__close{
    position: absolute;
    top: 15px;
    right: 15px;
    background: transparent;
    padding: 3px;
  }
  &-title {
    font-size: 24px;
    line-height: 120%;
    font-weight: 700;
  }
  &-form {
    margin-top: 30px;
    &__group {
      padding: 0;
      margin: 0;
      border: none;
      &:not(:first-child) {
        margin-top: 15px;
      }
      span {
        font-size: 14px;
        line-height: 120%;
        display: inline-block;
        position: relative;
        margin-bottom: 5px;
        &:after {
          content: '*';
          position: absolute;
          top: 0;
          right: -8px;
          font-size: 18px;
          color: #f25353
        }
      }
      input {
        display: block;
        width: 100%;
        height: 48px;
        padding: 0 15px;
        font-size: 16px;
        line-height: 120%;
        color: #081529;
        background: #f8f8f8;
        border: 1px solid #ddd;
      }
    }
    &__btn {
      width: 140px;
      height: 46px;
    }
  }
  &-btn {
    width: 200px;
    height: 46px;
  }
}
.breadcrumbs {
  &__items {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  &__item {
    font-size: 14px;
    line-height: 120%;
    color: #ffba01;
    position: relative;
    margin-top: 20px;
    &:hover {
      color: #ffffff;
    }
    &--active {
      color: #ffffff;
    }
    &:not(:last-child) {
      margin-right: 26px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -16px;
        transform: translate(0, -50%);
        width: 6px;
        height: 6px;
        background-color: #ffba01;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .services-cards {
    gap: 20px;
  }
  .services-card {
    &__title {
      font-size: 16px;
    }
    &__btn {
      width: 117px;
      height: 34px;
      font-size: 12px;
    }
    &__price {
      font-size: 12px;
    }
  }
  .services-hover {
    &__title {
      font-size: 14px;
    }
    &__price {
      font-size: 12px;
    }
  }
}

@media screen and (max-width: 991px) {
  .display2 {
    font-size: 36px;
  }
  .services-cards {
    grid-template-columns: 1fr 1fr 1fr;
    margin-top: 30px;
  }
  .services-btn {
    margin-top: 20px;
  }
}

@media screen and (max-width: 767px) {
  .services-cards {
    grid-template-columns: 1fr 1fr;
  }
  .services-card {
    &__desc {
      left: 15px;
      bottom: 15px;
      padding-right: 15px;
    }
    &__title {
      font-size: 14px;
    }
    &__btn {
      width: 109px;
      height: 37px;
      font-size: 11px;
    }
  }
  .services-hover {
    top: 15px;
    left: 15px;
    padding-right: 15px;
    &__title {
      font-size: 12px;
    }
  }
}

@media screen and (max-width: 575px) {
  .display2 {
    font-size: 30px;
  }
  .services-cards {
    gap: 15px;
    margin-top: 20px;
  }
  .services-card {
    &__desc {
      left: 10px;
      bottom: 10px;
    }
    &__title {
      font-size: 11px;
    }
    &__btn {
      width: 89px;
      height: 26px;
      font-size: 10px;
    }
    &__price {
      font-size: 11px;
    }
  }
  .services-hover {
    top: 10px;
    left: 10px;
    &-title {
      font-size: 11px;
    }
    &__price {
      font-size: 11px;
    }
  }
  .modal {
    &__dialog {
      padding: 30px 20px;
    }
    &-title {
      font-size: 20px;
    }
    &-form {
      margin-top: 20px;
      &__group {
        span {
          font-size: 12px;
          &:after {
            font-size: 14px;
          }
        }
        input {
          height: 35px;
          font-size: 14px;
        }
      }
      &__btn {
        height: 35px;
      }
    }
  }
}