.footer {
  background-color: #1a2a43;
  padding: 40px 0 60px 86px;
  &-items {
    display: flex;
    justify-content: space-between;
  }
  &-item {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    &:not(:first-child) {
      margin-left: 30px;
    }
  }
  &-logo {
    width: 140px;
    display: block;
    img {
      width: 100%;
    }
  }
  &-btns {
    margin-top: 30px;
  }
  &-btn {
    width: 168px;
    height: 46px;
  }
  &-socials {
    display: flex;
    align-items: center;
    margin-top: 12px;
  }
  &-social {
    max-width: 25px;
    &:not(:first-child) {
      margin-left: 15px;
    }
    img {
      width: 100%;
    }
  }
  &-list {
    li {
      font-size: 14px;
      line-height: 120%;
      color: #ffffff;
      padding-left: 14px;
      position: relative;
      &:not(:first-child) {
        margin-top: 15px;
      }
      &:hover {
        color: #ffba01;
      }
      &:after {
        content: '';
        position: absolute;
        top: 5px;
        left: 0;
        width: 5px;
        height: 5px;
        background-color: #ffba01;
      }
    }
  }
  &-brevis {
    display: flex;
    align-items: center;
    &__img {
      width: 35px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &__desc {
      font-size: 13px;
      line-height: 130%;
      color: #ffffff;
      margin-left: 12px;
      a {
        display: block;
        color: #ffba01;
      }
    }
  }
  .up {
    width: 80px;
    height: 46px;
    margin: 15px 0 0 auto;
  }
}

@media screen and (max-width: 1200px) {
  .footer {
    padding: 40px 0 40px 86px;
    &-inner {
      position: relative;
      padding-bottom: 76px;
    }
    &-item2 {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      flex-direction: row;
      margin-left: 0 !important;
    }
    .up {
      margin: 0 0 0 25px;
    }
  }
}

@media screen and (max-width: 991px) {
  .footer {
    &-item {
      justify-content: start;
      &:not(:first-child) {
        margin-left: 15px;
      }
    }
    &-item2 {
      justify-content: space-between;
    }
    &-btn {
      width: 141px;
      font-size: 12px;
    }
    &-list {
      li {
        font-size: 12px;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .footer {
    &-item--delete {
      display: none;
    }
  }
}

@media screen and (max-width: 575px) {
  .footer {
    padding: 30px 0 30px 66px;
    &-inner {
      padding-bottom: 0;
    }
    &-items {
      display: grid;
      grid-template-columns: 1fr;
      gap: 30px;
    }
    &-item {
      flex-direction: row;
      justify-content: space-between;
    }
    &-item2 {
      position: static;
    }
    &-item--delete2 {
      display: none;
    }
    &-logo {
      width: 115px;
    }
    &-btns {
      margin: 0 0 0 15px;
    }
    .up {
      height: 38px;
    }
  }
}

@media screen and (max-width: 399px) {
 .footer {
   padding-left: 0;
 }
}