.content {
  padding-left: 86px;
}
.sidebar {
  position: fixed;
  z-index: 4;
  top: 0;
  left: 0;
  width: 86px;
  height: 100vh;
  background-color: #081529;
  padding: 25px 10px;
  overflow-y: auto;
  &::-webkit-scrollbar {
    width: 0;
  }
  &::-webkit-scrollbar-track {
    background: #081529;
    border-radius: 3px;
  }
  &::-webkit-scrollbar-thumb {
    background: #ffffff;
    border-radius: 3px;
  }
  &--active {
    z-index: 2;
  }
  &-burger {
    width: 25px;
    height: 25px;
    position: relative;
    z-index: 10;
    cursor: pointer;
    margin: 0 auto;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 2px;
      background: #ffffff;
      transition: all 0.5s;
    }
    span:nth-child(1) {
      top: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
    &--active {
      span:nth-child(1) {
        top: 11px;
        transform: rotate(-45deg);
      }
      span:nth-child(2) {
        display: none;
      }
      span:nth-child(3) {
        bottom: 12px;
        transform: rotate(45deg);
      }
    }
  }
  &-menu {
    position: fixed;
    z-index: 4;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);;
    transition: all 0.5s;
    opacity: 0;
    pointer-events: none;
    &--active {
     opacity: 1;
     pointer-events: all;
      .sidebar-menu__inner {
        margin-left: 0;
      }
    }
  }
  &-menu__inner {
    width: 400px;
    height: 100%;
    background-color: #081529;
    padding: 80px 30px 60px;
    overflow-y: auto;
    margin-left: -105%;
    transition: all 0.5s;
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #081529;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #ffffff;
      border-radius: 3px;
    }
  }
  &-search {
    width: 26px;
    height: 26px;
    padding: 0;
    margin: 30px auto 0;
    background: transparent;
    img {
      width: 100%;
    }
  }
  &-socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 60px;
  }
  &-social {
    width: 26px;
    &:not(:first-child) {
      margin-top: 20px;
    }
    img {
      width: 100%;
    }
  }
  &-btn {
    width: 46px;
    height: 260px;
    margin: 60px auto 0;

    span {
      writing-mode: vertical-rl;
      transform: rotate(180deg);
    }
  }
  &-tel {
    display: flex;
    img {
      width: 16px;
    }
    span {
      font-size: 15px;
      line-height: 120%;
      font-weight: 700;
      text-transform: uppercase;
      color: #ffba01;
      margin-left: 6px;
    }
    &:hover {
      span {
        color: #c9a028;
      }
    }
  }
  &-mail {
    display: block;
    font-size: 15px;
    line-height: 120%;
    color: #ffba01;
    margin-top: 8px;
    padding-left: 22px;
    &:hover {
      span {
        color: #c9a028;
      }
    }
  }
  &-location {
    display: flex;
    margin-top: 25px;
    &__img {
      width: 12px;
    }
    &__desc {
      font-size: 15px;
      line-height: 120%;
      font-weight: 700;
      color: #ffffff;
      span {
        display: block;
      }
      span:nth-child(2) {
        font-weight: 400;
      }
    }
  }
  &-list {
    margin-top: 60px;
    li:not(:first-child) {
      margin-top: 25px;
    }
  }
  &-link {
    font-size: 14px;
    line-height: 120%;
    font-weight: 700;
    color: #ffffff;
    &:hover {
      color: #ffba01;
    }
  }
  &-item {
    position: relative;
    &--active {
      .sidebar-show {
        span {
          color: #ffba01;
        }
        svg path {
          fill: #ffba01;
        }
      }
    }
  }
  &-show {
    cursor: pointer;
    svg path {
      fill: #ffffff;
    }
    span {
      font-size: 14px;
      line-height: 120%;
      font-weight: 700;
      color: #ffffff;
    }
    &:hover {
      span {
        color: #ffba01;
      }
      svg path {
        fill: #ffba01;
      }
    }
  }
  &-hidden {
    padding: 15px;
    display: none;
  }
  &-hidden__list a {
    display: block;
    font-size: 14px;
    line-height: 120%;
    font-weight: 700;
    color: #ffffff;
    padding-left: 17px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 5px;
      height: 5px;
      background-color: #ffba01;
    }
    &:not(:first-child) {
      margin-top: 15px;
    }
    &:hover {
      color: #ffba01;
    }
  }
}
.main {
  &-btns {
    position: relative;
  }
  &-slide {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 700px 0 60px;
  }
  &-title {
    font-size: 55px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    color: #ffffff;
  }
  &-text {
    font-size: 20px;
    line-height: 150%;
    color: #ffffff;
    max-width: 750px;
    margin: 27px 0 0;
  }
  &-btn {
    width: 130px;
    height: 46px;
    margin-top: 27px;
  }
  &-slider__btns {
    display: flex;
    align-items: center;
    position: absolute;
    left: 145px;
    bottom: 60px;
  }
  &-button__prev {
    position: static;
    z-index: 2;
    width: 46px;
    height: 46px;
    background-color: #efefef;
    &:hover {
      background: #ffba01;
      svg path {
        fill: #ffffff;
      }
    }
    svg {
      width: 25px;
      height: auto;
      transform: rotate(180deg);
    }
    &:after {
      display: none;
    }
  }
  &-button__next {
    position: static;
    z-index: 2;
    width: 46px;
    height: 46px;
    background-color: #efefef;
    margin-left: 15px;
    &:hover {
      background: #ffba01;
      svg path {
        fill: #ffffff;
      }
    }
    svg {
      width: 25px;
      height: auto;
    }
    &:after {
      display: none;
    }
  }
}
.services {
  padding: 60px 0;
}
.materials {
  padding: 60px 0;
  background-color: #e4e4e4;
  &-cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 30px;
    margin-top: 40px;
  }
  &-card {
    position: relative;
    &:hover {
      img {
        transform: scale(1.2);
      }
    }
    &__img {
      overflow: hidden;
      img {
        display: block;
        width: 100%;
        transition: all 0.5s;
      }
    }
    &__title {
      position: absolute;
      left: 20px;
      bottom: 20px;
      padding-right: 20px;
      font-size: 19px;
      line-height: 130%;
      font-weight: 700;
      text-transform: uppercase;
      color: #ffffff;
    }
  }
  &-btn {
    width: 150px;
    height: 46px;
    margin: 30px auto 0;
  }
}
.about {
  overflow: hidden;
  .container {
    padding: 0 0 0 30px;
  }
  &-inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
  }
  &-left {
    max-width: 520px;
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-right: 80px;
    padding: 60px 0;
  }
  &-right {
    max-width: calc(100% - 600px);
  }
  &-desc {
    margin-top: 50px;
    p {
      font-size: 16px;
      line-height: 140%;
      color: #081529;
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
  }
  &-btns {
    display: flex;
    align-items: center;
    margin-top: 30px;
  }
  &-btn {
    width: 150px;
    height: 46px;
  }
  &-btn2 {
    width: 153px;
    height: 46px;
    margin-left: 20px;
  }
  &-img {
    width: 950px;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
      display: block;
    }
  }
}
