.connection {
  padding: 60px 0;
  &-subtitle {
    font-size: 16px;
    line-height: 120%;
    color: #081529;
    margin-top: 40px;
  }
  &-form {
    margin-top: 30px;
    &__grid {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 20px 30px;
    }
    &__flex {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 20px !important;
    }
    &__group {
      margin: 0;
      padding: 0;
      border: unset;
      position: relative;
      img {
        position: absolute;
        top: 50%;
        left: 20px;
        transform: translate(0, -50%);
      }
      input {
        width: 100%;
        height: 60px;
        background-color: #f1f1f1;
        border: 1px solid transparent;
        font-size: 16px;
        line-height: 120%;
        color: #081529;
        padding: 0 10px 0 50px;
      }
    }
    &__group3 {
      grid-column: 1/4;
    }
    &__textarea {
      width: 100%;
      height: 120px;
      background-color: #f1f1f1;
      border: 1px solid transparent;
      padding: 20px 20px 20px 50px;
      resize: none;
      font-size: 16px;
      line-height: 120%;
      color: #081529;
    }
    &__info {
      font-size: 16px;
      line-height: 120%;
      color: #081529
    }
    &__btn {
      flex-shrink: 0;
      width: 130px;
      height: 46px;
      margin-left: 60px;
    }
  }
  .icon-textarea {
    top: 22px;
    transform: translate(0, 0);
  }
}
.contacts {
  position: relative;
  overflow: hidden;
  .map{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
    }
  }
  &-inner {
    max-width: 370px;
    background: rgba(241, 241, 241, 0.8);
    padding: 170px 0;
    display: flex;
    justify-content: center;
    flex-direction: column;
    position: relative;
    z-index: 2;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: 100%;
      width: 10000px;
      height: 100%;
      background: rgba(241, 241, 241, 0.8);
    }
  }
  &-title {
    color: #ffffff;
  }
  &-socials {
    margin-top: 50px;
  }
  &-social {
    display: flex;
    &:not(:first-child) {
      margin-top: 35px;
    }
    span {
      font-size: 15px;
      line-height: 120%;
      font-weight: 700;
      color: #081529;
      margin-left: 12px;
    }
    &__location {
      font-size: 15px;
      line-height: 120%;
      font-weight: 700;
      color: #081529;
      margin-left: 12px;
      span {
        display: block;
        font-weight: 400;
        margin: 7px 0 0 0;
      }
    }
  }
}


