.service-main {
  background-image: url("../img/main_bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 215px 0 30px;
  &__title {
    color: #ffffff !important;
  }
}
.info {
  padding: 60px 0;
  &-top {
    display: flex;
    align-items: stretch;
  }
  &-left {
    max-width: 550px;
    width: 100%;
    height: auto;
    margin-right: 50px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &-right {
    max-width: calc(100% - 600px);
    width: 100%;
  }
  &-price {
    font-size: 26px;
    line-height: 105%;
    font-weight: 700;
    letter-spacing: -0.02em;
    color: #000000;
    span {
      font-size: 26px;
      font-weight: 400;
      display: block;
      margin-bottom: 10px;
    }
    i {
      font-style: normal;
      font-size: 42px;
    }
  }
  &-form {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    margin-top: 25px;
    &__group {
      padding: 0;
      margin: 0;
      border: unset;
      position: relative;
      &:not(:first-child) {
        margin-left: 20px;
      }
      img {
        position: absolute;
        top: 50%;
        left: 20px;
        transform: translate(0, -50%);
      }
      input {
        width: 100%;
        height: 60px;
        background-color: #f1f1f1;
        border: 1px solid transparent;
        padding: 0 15px 0 50px;
        font-size: 16px;
        line-height: 120%;
        color: #081529;
      }
    }
    &__group2 {
      width: 100%;
    }
    &__btn {
      width: 188px;
      height: 60px;
    }
  }
  &-items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: 1fr;
    border: 5px solid #f1f1f1;
    margin-top: 33px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      height: 1px;
      background: #f1f1f1;
    }
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 1px;
      height: 100%;
      background: #f1f1f1;
    }
  }
  &-item {
    display: flex;
    align-items: center;
    padding: 25px 15px;
    &__img {
      width: 50px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &__desc {
      font-size: 16px;
      line-height: 120%;
      font-weight: 700;
      letter-spacing: -0.02em;
      color: #000000;
      margin-left: 20px;
    }
  }
  &-items__mob {
    display: none;
  }
  &-desc {
    margin-top: 35px;
    p {
      font-size: 16px;
      line-height: 140%;
      color: #081529;
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
  }
  &-list {
    margin-top: 30px;
    li {
      font-size: 16px;
      line-height: 120%;;
      color: #081529;
      padding-left: 22px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 5px;
        left: 0;
        width: 9px;
        height: 9px;
        background-color: #ffba01;
      }
      &:not(:first-child) {
        margin-top: 12px;
      }
    }
  }
}
.partners {
  padding: 60px 0;
  &-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &-slider__btns {
    display: flex;
    align-items: center;
    margin-left: 15px;
  }
  &-slider {
    position: relative;
    margin-top: 65px;
  }
  &-wrapper {
    align-items: center;
  }
  &-slide {
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      width: 100%;
    }
  }
  &-slide2 {
    img {
      width: auto;
    }
  }
  &-button__prev {
    position: static;
    z-index: 2;
    width: 46px;
    height: 46px;
    background-color: #efefef;
    &:hover {
      background: #ffba01;
      svg path {
        fill: #ffffff;
      }
    }
    svg {
      width: 25px;
      height: auto;
      transform: rotate(180deg);
    }
    &:after {
      display: none;
    }
  }
  &-button__next {
    position: static;
    z-index: 2;
    width: 46px;
    height: 46px;
    background-color: #efefef;
    margin-left: 15px;
    &:hover {
      background: #ffba01;
      svg path {
        fill: #ffffff;
      }
    }
    svg {
      width: 25px;
      height: auto;
    }
    &:after {
      display: none;
    }
  }
}

@media screen and (max-width: 1300px) {
  .info {
    &-form {
      &__btn {
        width: 168px;
      }
    }
    &-item {
     flex-direction: column;
      justify-content: center;
      text-align: center;
      &__desc {
        margin: 7px 0 0 0;
        br {
          display: none;
        }
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .info {
    &-left {
      max-width: 400px;
      margin-right: 30px;
    }
    &-right {
     max-width: calc(100% - 430px);
    }
  }
}

@media screen and (max-width: 991px) {
  .service-main {
    padding: 135px 0 30px;
  }
  .info {
    &-left {
      max-width: 280px;
      margin-right: 20px;
    }
    &-right {
      max-width: calc(100% - 300px);
    }
    &-price {
      font-size: 24px;
      span {
        font-size: 24px;
      }
      i {
        font-size: 36px;
      }
    }
    &-form {
      &__group {
        &:not(:first-child) {
          margin-left: 12px;
        }
      }
      &__btn {
        width: 100px;
        font-size: 12px;
      }
    }
    &-items {
      display: none;
    }
    &-items__mob {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      grid-auto-rows: 1fr;
      border: 5px solid #f1f1f1;
      margin-top: 20px;
      position: relative;
    }
    &-item {
      position: relative;
      &:not(:first-child) {
        &:after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 1px;
          height: 100%;
          background-color: #f1f1f1;
        }
      }
      &__desc {
        font-size: 14px;
      }
    }
  }
  .partners {
    &-slider {
      margin-top: 50px;
    }
  }
}

@media screen and (max-width: 767px) {
  .info {
    padding: 30px 0 40px 0;
    &-left {
      max-width: 220px;
    }
    &-right {
      max-width: calc(100% - 240px);
    }
    &-price {
      font-size: 16px;
      span {
        font-size: 20px;
      }
      i {
        font-size: 24px;
      }
    }
    &-form {
      display: block;
      &__group {
        &:not(:first-child) {
          margin: 10px 0 0 0;
        }
        input {
          height: 50px;
          font-size: 14px;
        }
      }
      &__btn {
        width: 168px;
        height: 50px;
      }
    }
    &-item {
      padding: 20px 15px;
      &__img {
        width: 30px;
      }
      &__desc {
        font-size: 10px;
      }
    }
  }
  .partners {
    padding: 40px 0;
  }
}

@media screen and (max-width: 575px) {
  .info {
    &-top {
      display: block;
    }
    &-left {
      max-width: 100%;
      margin: 0;
    }
    &-right {
      max-width: 100%;
      margin-top: 25px;
    }
    &-form {
      display: flex;
      &__group {
        &:not(:first-child) {
          margin: 0 0 0 10px;
        }
      }
      &__btn {
        width: 90px;
      }
    }
    &-items__mob {
      grid-template-columns: 1fr 1fr;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        height: 1px;
        background: #f1f1f1;
      }
      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 1px;
        height: 100%;
        background: #f1f1f1;
      }
    }
    &-item {
      &:not(:first-child) {
        &:after {
          display: none;
        }
      }
      &__desc {
        font-size: 12px;
      }
    }
    &-desc {
      margin-top: 25px;
      p {
        font-size: 14px;
        &:not(:first-child) {
          margin-top: 20px;
        }
      }
    }
    &-list {
      margin-top: 25px;
      li {
        font-size: 14px;
        &:after {
          top: 3.5px;
        }
      }
    }
  }
  .partners {
    &-top {
      display: block;
    }
    &-slider {
      margin-top: 40px;
    }
    &-slider__btns {
      margin-top: 37px;
    }
  }
}

@media screen and (max-width: 399px) {
  .info {
    &-form {
      display: block;
      &__group {
        &:not(:first-child) {
          margin: 10px 0 0 0;
        }
      }
      &__btn {
        width: 168px;
      }
    }
  }
}